.projects__title {
  @include spacing(margin, $top: 0.5em, $bottom: 0.5em);
  @include text($align: center);
}

.project {
  @include spacing(padding, $bottom: 5em);

  &:last-of-type {
    @include spacing(padding, $bottom: 2em);
  }

  @include breakpoint-up(medium){
    display: flex;
  }

}

.project:nth-of-type(2n) .projects__image {

  @include breakpoint-up(medium){
    order: 1;
  }
}

.projects__image,
.projects__text {

  @include text($align: center);

  @include breakpoint-up(medium){
    width: 50%;
  }
}

.projects__header {
  @include spacing(bottom, $bottom: 0.5em);
  @include text($size: 1.25rem, $align: center);
}

.projects__about a {
  position: relative;
  white-space: nowrap; //because otherwise the after looks terrible

  &::after {
    @include link-after(0, 4px, 2px, 100%, $primary-colour);
    transform: skew(-24deg) rotate(-.5deg) scale(0);
  }

  &:hover::after {
    transform: skew(-24deg) rotate(-.5deg) scale(1);
  }
}

.projects__languages {
  @include spacing(margin, $top: 0.5em);
  @include text($weight: 300, $align: center);
}

.projects__buttons {
  display: flex;
  justify-content: center;
  @include spacing(margin, $top: 0.5em);
}

.projects__button {
  position: relative;
  @include spacing(padding, $top: 1em, $right: 1.5em, $bottom: 1em, $left: 1.5em);
  border: 2px solid $secondary-colour;
  background: white;
  @include text($size: 0.75rem, $weight: 600, $colour: $secondary-colour, $transform: uppercase);
  cursor: pointer;
  z-index: 1;
  transition: color 300ms ease-in-out;

  &:not(:last-of-type) {
    margin-left: 2em;
    @include spacing(margin, $right: 2em);
  }

  &::after {
    @include link-after(0, 0, 100%, 100%, $secondary-colour);
    z-index: -1;
    transform: scale(0);
    transform-origin: top right;
  }

  &:hover::after {
    transform: scale(1);
    transform-origin: bottom left;
  }

  &:hover {
    @include text($colour: white);
  }

  &:active {
    position: relative;
    top: 2px;
  }
}
